
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'GsInputText',
  inheritAttrs: false,
  props: {
    dirname: {
      type: String,
      default: undefined,
    },
    name: {
      type: String,
      default: '',
    },
    maxlength: {
      type: [Number, String],
      default: undefined,
    },
    minlength: {
      type: [Number, String],
      default: undefined,
    },
    type: {
      type: String,
      default: 'text',
    },
    valid: {
      type: Boolean,
      default: null,
    },
    value: {
      type: [String, Number],
      default: '',
    },
    prefix: {
      type: String,
      default: undefined,
    },
    autocomplete: {
      type: [Boolean, String],
      default: undefined,
    },
    autofocus: {
      type: [Boolean, String],
      default: undefined,
    },
    id: {
      type: String,
      default: undefined,
    },
    inputmode: {
      type: String,
      default: undefined,
    },
    readonly: {
      type: [Boolean, String],
      default: undefined,
    },
    required: {
      type: [Boolean, String],
      default: undefined,
    },
    tabindex: {
      type: [Number, String],
      default: undefined,
    },
    title: {
      type: String,
      default: undefined,
    },
    placeholder: {
      type: String,
      default: undefined,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    hasFocus: false,
    internalValue: '',
  }),
  computed: {
    isValid: (vm: { valid: boolean }) => vm.valid === true,
    isInvalid: (vm: { valid: boolean }) => vm.valid === false,
  },
});
